.generator-container {
    max-width: 400px;
    margin: 40px auto;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 4px 12px rgba(0,0,0,0.1);
    background: white;
    text-align: center;
  }
  
  .controls label {
    display: block;
    margin-top: 10px;
    font-weight: bold;
  }
  
  .controls select, .controls input[type="range"] {
    margin: 10px 0;
    width: 100%;
  }
  
  .output-box {
    margin-top: 20px;
  }
  
  .output-box input {
    width: 100%;
    padding: 10px;
    font-size: 1rem;
    margin-bottom: 10px;
  }
  
  .button-row button {
    margin: 5px;
    padding: 8px 12px;
    font-size: 0.9rem;
    cursor: pointer;
  }
  
  .strength-meter {
    margin-top: 10px;
    height: 10px;
    width: 100%;
    background-color: #eee;
    border-radius: 5px;
    overflow: hidden;
  }
  
  .strength-meter .bar {
    height: 100%;
    transition: width 0.3s ease;
  }
  