/* App.css */

body {
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(135deg, #6e8efb, #a777e3);
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App {
  text-align: center;
  background: #ffffff;
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 8px 16px rgba(0,0,0,0.2);
  width: 90%;
  max-width: 500px;
}

h1 {
  margin-bottom: 1.5rem;
  color: #4a4a4a;
  font-size: 2rem;
}

/* Make sure inputs and buttons look good */
input, button, select {
  padding: 10px 15px;
  margin: 10px 0;
  width: 100%;
  box-sizing: border-box;
  border-radius: 8px;
  border: 1px solid #ccc;
}

button {
  background-color: #6e8efb;
  color: white;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #4f6cdf;
}

/* Password strength meter bar (optional customization) */
.strength-meter {
  height: 10px;
  border-radius: 5px;
  margin-top: 10px;
  background-color: #ccc;
}

.strength-meter.low {
  background-color: red;
}

.strength-meter.medium {
  background-color: orange;
}

.strength-meter.high {
  background-color: green;
}

/* Responsive tweaks */
@media (max-width: 600px) {
  .App {
    padding: 1rem;
    border-radius: 8px;
  }

  h1 {
    font-size: 1.5rem;
  }
}
